

.links-and-attributions h2 {
    color: #2e2e2e;
}

.links-and-attributions ul {
    list-style-type: none;
    padding: 0;
    margin-top: 10px;
}

.links-and-attributions ul li {
    margin-bottom: 10px;
}

.links-and-attributions ul li a {
    color: #0077cc;
    text-decoration: none;
}

.links-and-attributions ul li a:hover {
    text-decoration: underline;
}

.links-and-attributions p {
    color: #333333;
    line-height: 1.5;
}

.next-link {
    display: block;
    margin-top: 30px;
    text-align: center;
    color: #2e2e2e;
    text-decoration: none;
    font-weight: bold;
}
